import React from 'react'
import './chart.css'
import {
    LineChart,
    Line,
    XAxis,
    // YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer
} from 'recharts';

function Charts({title, data, dataKey, grid}) {
    return (
        <div className='chart'>
            <h3 className="chartTiles">{title}</h3>
            <ResponsiveContainer width="100%" aspect={4 / 1}>
                <LineChart data={data}>
                    <XAxis dataKey="name"/>
                    <Line type="monotone" dataKey={dataKey} stroke='#5550bd'/>
                    <Tooltip />
                   {grid && <CartesianGrid stroke='#e0dfdf' strokeDasharray="5 5"/>}
                    <Legend />
                </LineChart>
            </ResponsiveContainer>
        </div>
    )
}

export default Charts